<template>
  <div>
    <v-breadcrumbs
      :items="items"
      divider="/"
    ></v-breadcrumbs>
    <v-card>
      <v-card-title>Groups List</v-card-title>
      <v-data-table
        :search="search"
        :headers="headers"
        :items="courses"
        item-key="full_name"
        class="table-rounded"
      >
        <!-- name -->
        <template v-slot:top>
          <v-text-field
            v-model="search"
            label="Search..."
            class="mx-4"
          ></v-text-field>
        </template>
        <template #[`item.course`]="{item}">
          {{ `${item.course.toUpperCase()}` }}
        </template>
        <template #[`item.actions`]="{item}">
          <v-icon
            small
            @click="selectUpdate(item)"
          >
            {{ icons.mdiBookEdit }}
          </v-icon>
        </template>
      </v-data-table>
    </v-card>
  </div>
</template>

<script>
import { mdiSquareEditOutline, mdiDotsVertical, mdiBookEdit } from '@mdi/js'
import {
  mapGetters,
  mapActions,
} from 'vuex'

export default {
  setup() {
    return {
      items: [
        {
          text: 'Groups',
          disabled: true,
          href: '#',
        },
      ],
      search: '',
      headers: [
        { text: 'CODE', value: 'code' },
        { text: 'COURSE', value: 'course' },
        { text: 'ACTIONS', value: 'actions', sortable: false },
      ],

      // icons
      icons: {
        mdiSquareEditOutline,
        mdiDotsVertical,
        mdiBookEdit,
      },
    }
  },
  methods: {
    // eslint-disable-next-line no-unused-vars
    selectUpdate(item) {
      alert(item)
    },
    ...mapActions(['getCourses']),
  },
  computed: {
    ...mapGetters(['courses']),
  },
  mounted() {
    this.getCourses()
  },
}
</script>
